import React from 'react';
import classes from './Footer.module.css'
const Footer = () => {
    return (
        <div className={classes.container}>
            <h1 className={classes.line1}>This site was made with spaghetti by <span>Brandon Joe</span></h1>
            <h2 className={classes.line2}>Although this site was fully designed and developed by me, inspiration was can be found at <span>playValorant.com</span> and the <span>Readme</span></h2>
        </div>
    );
};

export default Footer;